@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
  margin: 1;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
  margin:0;
}
body {
  font-size: 92%;
  font-family: 'PT Serif', serif;
  color: #0f2235;
  background-image: url(/simple-react-website/static/media/1.ec243b7e.jpg);
  height: 100vh;
  background-size: cover;
  text-align: center;
 
  background-position: center;
  background-attachment: fixed;
}
h2, h3, h4, h5 {
  margin: 0 0 3px;
}


.listOfCards {
  display: inline;
  flex-wrap: wrap;
}

.UserCard {  
  display: inline-block;
  width: 290px;
  color:black;
  font-family: 'Poppins', sans-serif;
  height: 360px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px black;
  margin: 10px;
  background: #0c2b5e; 
  background: -webkit-gradient(linear,right top, left top,from(#cdcdcd),to(white)); 
  background: linear-gradient(to left,#cdcdcd,white)
}

.UserCard img {

  width: 200px;
  height: 200px;
  margin: 20px 0 0 0;
  border-radius: 50%;
  border: 4px solid #0f2235;
  object-fit: cover;
}

.UserCardTop {
  height: 67%;
  width: 100%;
  overflow: hidden;
  text-align: center;
}

.UserCardBottom {
  min-height: 40%;
  overflow: auto;
  min-width: 100%;
  max-width: 100%;
  padding: 0 10px 5px;
  overflow-wrap: break-word;
}

.UserCardBottom::-webkit-scrollbar {width: 3px; height:1px;-webkit-transition:all .5s;transition:all .5s;z-index:10;}
.UserCardBottom::-webkit-scrollbar-track {background-color: #0c2b5e;}
.UserCardBottom::-webkit-scrollbar-thumb {
  background-color: #bec4c8; 
  border-radius:3px;
}


.subhead {
    font-size: 300%;
    color: #0f2235;
    font-family: 'Oxanium';
}

.listOfCards {
    display: inline;
    flex-wrap: wrap;
  }
  
  .ProjectCard {  
    display: inline-block;
    width: 452px;
    color: #0f2235;
    height: 300px;
    border-radius: 25px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.8);
    margin: 10px;
    background: -webkit-gradient(linear,right top, left top,from(#e4c0c6),to(white));
    background: linear-gradient(to left,#e4c0c6,white)
  }
  
  .ProjectCard img {
    float: left;
    width: 226px;
    height: 290px;
    margin-top: 5px;
    border-radius: 10%;
    padding: 1em;
    object-fit: cover;
  }
  
  .ProjectCardTop {
    height: 22%;
    overflow: visible;
  }
  

  .ProjectCardBottom::-webkit-scrollbar {width: 3px; height:1px;-webkit-transition:all .5s;transition:all .5s;z-index:10;}
  .ProjectCardBottom::-webkit-scrollbar-track {background-color: #0c2b5e;}
  .ProjectCardBottom::-webkit-scrollbar-thumb {
    background-color: #bec4c8; 
    border-radius:3px;
  }
  
 

  .topnav {
    
